import React from 'react';
import { Link } from 'react-router-dom';

const NAVBAR = () => (
    <nav>
        <ul>
            <li>
                <Link to="/">home</Link>
            </li>
            <li>
                <Link to="/about">about</Link>
            </li>
            <li>
                <Link to="/articles-list">articles</Link>
            </li>
        </ul>
    </nav>
)

export default NAVBAR;