import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import HOMEPAGE from './pages/homepage';
import ABOUT_PAGE from './pages/aboutpage';
import ARTICLES_LIST_PAGE from './pages/articleslistpage';
import ARTICLE_PAGE from './pages/articlepage';
import NOT_FOUND_PAGE from './pages/notfoundpage';
import NAVBAR from './navbar';
import './App.css';

function App() {
  return (
    <Router>
      <div className="App">
        <NAVBAR />
        <div id="page-body">
          <Switch>
            <Route path="/" component={HOMEPAGE} exact />
            <Route path="/about" component={ABOUT_PAGE} />
            <Route path="/articles-list" component={ARTICLES_LIST_PAGE} />
            <Route path="/article/:name" component={ARTICLE_PAGE} />
            <Route component={NOT_FOUND_PAGE} />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
