import React, { useState } from 'react';
import ARTICLE_PAGE from '../pages/articlepage';

const ADD_COMMENT_FORM = ({ ARTICLE_NAME, SET_ARTICLE_INFO }) => {
  const [username, setUsername] = useState('');
  const [commentText, setCommentText] = useState('');

  const ADD_COMMENT = async () => {
    const RESULT = await fetch(`/api/articles/${ARTICLE_NAME}/add-comment`, {
      method: 'post',
      body: JSON.stringify({ username, text: commentText }),
      headers: {
        'Content-Type': 'application/json',
      }
    });
    const BODY = await RESULT.json();
    SET_ARTICLE_INFO(BODY);
    setUsername('');
    setCommentText('');
  }

  return (
    <div id="add-comment-form">
      <h3>add a comment</h3>
      <label>
        name:
        <input type="text" value={username} onChange={(event) => setUsername(event.target.value)} />
      </label>
      <label>
        comment:
        <textarea rows="4" cols="50" value={commentText} onChange={(event) => setCommentText(event.target.value)} />
      </label>
      <button onClick={() => ADD_COMMENT()}>add comment</button>
    </div>
  );
}

export default ADD_COMMENT_FORM;