import React from 'react';
import ARTICLES_LIST from './articleslist';

const UPVOTES_SECTION = ({ ARTICLES_NAME, upvotes, SET_ARTICLE_INFO }) => {
  const UPVOTE_ARTICLE = async () => {
    const RESULT = await fetch(`/api/articles/${ARTICLES_NAME}/upvote`, {
      method: 'post',
    });
    const BODY = await RESULT.json();
    SET_ARTICLE_INFO(BODY);
  }
  return (
  <div id="upvotes-section">
    <button onClick={() => UPVOTE_ARTICLE()}>add button</button>
    <p>this post has been upvoted {upvotes} times</p>
  </div>
  );
};

export default UPVOTES_SECTION;