import React, { useState, useEffect } from 'react';
import ARTICLES_LIST from '../components/articleslist';
import COMMENTS_LIST from '../components/commentslist';
import UPVOTES_SECTION from '../components/upvotessection';
import ADD_COMMENT_FORM from '../components/addcommentform';
import NOT_FOUND_PAGE from './notfoundpage';
import articleContent from './article-content';

const ARTICLE_PAGE = ({ match }) => {
  const NAME = match.params.name;
  const ARTICLE = articleContent.find(ARTICLE => ARTICLE.name === NAME);

  const [ARTICLE_INFO, SET_ARTICLE_INFO] = useState({ upvotes: 0, comments: [] });

  useEffect(() => {
    const FETCH_DATA = async () => {
      const RESULT = await fetch(`/api/articles/${NAME}`);
      const BODY = await RESULT.json();
      console.log(BODY);
      SET_ARTICLE_INFO(BODY);
    }
    FETCH_DATA();
  }, [NAME]);

  if (!ARTICLE) return <NOT_FOUND_PAGE />

  const OTHER_ARTICLES = articleContent.filter(ARTICLE => ARTICLE.name !== NAME);

  return (
  <>
    <h1>{ARTICLE.title}</h1>
    <UPVOTES_SECTION ARTICLES_NAME={NAME} upvotes={ARTICLE_INFO.upvotes} SET_ARTICLE_INFO={SET_ARTICLE_INFO} />
    {ARTICLE.content.map((paragraph, key) => (
      <p key={key}>{paragraph}</p>
    ))}
    <COMMENTS_LIST comments={ARTICLE_INFO.comments} />
    <ADD_COMMENT_FORM ARTICLE_NAME={NAME} SET_ARTICLE_INFO={SET_ARTICLE_INFO} />
    <h3>other articles:</h3>
    <ARTICLES_LIST articles={OTHER_ARTICLES} />
  </>
)};

export default ARTICLE_PAGE;